<template>
  <div id="sub-main"></div>
</template>
<script setup>
import {onMounted} from 'vue'
import { start } from 'qiankun';
/**
 * 添加子应用入口，这是依赖主应用的路由匹配
 * 本来是想直接覆盖主应用的入口路由的，但是发现那样的话当跳转到子应用后->点返回，主应用的路由入口就会消失从而报错
 * 所以解决方案是 让子应用依赖主应用的其中一个路由
 */
onMounted(() => {
  if(!window.qiankunStarted) {
    window.qiankunStarted = true
    start()
  }
})
</script>
